import style from "./InternalFees.module.css";

const InternalFeesDetailCard = ({
  state,
  student,
  onMarkOffline,
  onMarkExempt,
}) => {
  // console.info("state", state);
  const onOffline = () => {
    onMarkOffline(student?._id);
  };
  const onExempt = () => {
    onMarkExempt(student?._id);
  };
  return (
    <>
      <div className={style.ifdl_card}>
        <h6>{student?.studentROLLNO ?? ""}.</h6>
        <h6>
          {student?.studentFirstName ?? ""}{" "}
          {student?.studentMiddleName ? student?.studentMiddleName : " "}{" "}
          {student?.studentLastName ?? ""}
        </h6>

        <img
          src={
            state?.exemptQuery?.includes(student?._id)
              ? "/images/radio-active.svg"
              : "/images/radio-disabled.svg"
          }
          alt="icon"
          onClick={onExempt}
        />

        <img
          src={
            state?.onlineList?.includes(student?._id)
              ? "/images/radio-active.svg"
              : "/images/radio-disabled.svg"
          }
          alt="icon"
        />

        <img
          src={
            state?.offlineQuery?.includes(student?._id)
              ? "/images/radio-active.svg"
              : "/images/radio-disabled.svg"
          }
          alt="icon"
          onClick={onOffline}
        />
      </div>
    </>
  );
};

export default InternalFeesDetailCard;
