import { useTranslation } from "react-i18next";
import style from "./InternalFees.module.css";
import InternalFeesDetailCard from "./InternalFeesDetailCard";
const InternalFeesDetailList = ({ students, state, setState }) => {
  const { t } = useTranslation();
  // console.info("students", students);

  const onMarkOffline = (val) => {
    setState((prev) => ({
      ...prev,
      offlineQuery: prev?.offlineQuery?.includes(val)
        ? prev?.offlineQuery?.filter((ids) => ids !== val)
        : [...prev?.offlineQuery, val],
      exemptQuery: prev?.exemptQuery?.includes(val)
        ? prev?.exemptQuery?.filter((ids) => ids !== val)
        : prev?.exemptQuery,
    }));
  };
  const onMarkExempt = (val) => {
    setState((prev) => ({
      ...prev,
      offlineQuery: prev?.offlineQuery?.includes(val)
        ? prev?.offlineQuery?.filter((ids) => ids !== val)
        : prev?.offlineQuery,
      exemptQuery: prev?.exemptQuery?.includes(val)
        ? prev?.exemptQuery?.filter((ids) => ids !== val)
        : [...prev?.exemptQuery, val],
    }));
  };

  return (
    <>
      <div className={style.ifdl_heading}>
        <h6>{t("roll_no")}</h6>
        <h6>{t("name")}</h6>
        <h6>{t("exemted")}</h6>
        <h6>{t("online")}</h6>
        <h6>{t("offline")}</h6>
      </div>
      {students?.map((student) => (
        <InternalFeesDetailCard
          student={student}
          state={state}
          onMarkOffline={onMarkOffline}
          onMarkExempt={onMarkExempt}
        />
      ))}
    </>
  );
};

export default InternalFeesDetailList;
