import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import AddButton from "../../../Ui/Button/AddButton";
import AddButtonWrapper from "../../../Ui/Button/AddButtonWrapper";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../FinanceManager.module.css";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useLocation } from "react-router-dom";
import {
  useFinanceInternalFeesCollect,
  useFinanceInternalFeesDetail,
} from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import InternalFeesDetailList from "./InternalFeesDetailList";
const InternalFeesDetail = ({ fid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    offlineQuery: [],
    exemptQuery: [],
  });
  const [prevState, setPrevState] = useState({
    offlineQuery: [],
    exemptQuery: [],
    onlineList: [],
  });
  const [student, setStudent] = useState([]);

  const [financeInternalFeesCollect] = useFinanceInternalFeesCollect();
  const { financeInternalFeesDetail, financeInternalFeesDetailLoading } =
    useFinanceInternalFeesDetail({
      fsid: getQuery.state?.fsid,
      skip: !getQuery.state?.fsid,
    });

  useEffect(() => {
    if (financeInternalFeesDetail?.all_students?.length) {
      setStudent(financeInternalFeesDetail?.all_students);
    }
    if (financeInternalFeesDetail?.one_fees?._id) {
      setPrevState((prev) => ({
        ...prev,
        offlineQuery: financeInternalFeesDetail?.one_fees?.offlineStudentsList,
        exemptQuery: financeInternalFeesDetail?.one_fees?.exemptList,
        onlineList: financeInternalFeesDetail?.one_fees?.onlineList,
      }));
      setStudent(financeInternalFeesDetail?.all_students);
    }
  }, [
    financeInternalFeesDetail?.all_students,
    financeInternalFeesDetail?.one_fees?._id,
    financeInternalFeesDetail?.one_fees?.offlineStudentsList,
    financeInternalFeesDetail?.one_fees?.exemptList,
    financeInternalFeesDetail?.one_fees?.onlineList,
  ]);
  const onCollectFees = () => {
    if (
      fid &&
      getQuery.state?.fsid &&
      (state.exemptQuery?.length > 0 || state.offlineQuery?.length > 0)
    ) {
      setDisabled((pre) => !pre);
      financeInternalFeesCollect({
        fid: fid,
        fsid: getQuery.state?.fsid,
        internalFees: state,
      })
        .then(() => {
          setState({
            offlineQuery: [],
            exemptQuery: [],
          });
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  // console.info("getQuery", getQuery.state);
  const onSearchEvent = (e) => {
    if (e.target.value) {
      let filterData = getFilterStudent(
        financeInternalFeesDetail?.all_students,
        e.target.value
      );
      setStudent(() => filterData);
      setSearch(e.target.value);
    } else {
      setStudent(financeInternalFeesDetail?.all_students);
      setSearch("");
    }
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery.state?.feesName ?? ""}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <AddButtonWrapper>
            <AddButton label="collect_fees" onAction={onCollectFees} />
          </AddButtonWrapper>
          <InternalFeesDetailList
            students={student}
            state={{
              offlineQuery: [...state.offlineQuery, ...prevState.offlineQuery],
              exemptQuery: [...state.exemptQuery, ...prevState.exemptQuery],
              onlineList: prevState.onlineList,
            }}
            setState={setState}
          />
          {financeInternalFeesDetailLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            !student?.length && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No student found related this search."
              />
            )
          ) : (
            !financeInternalFeesDetail?.all_students?.length && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No student found."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default InternalFeesDetail;
const getFilterStudent = (list, search) => {
  return list?.filter((val) => {
    if (
      val?.valid_full_name?.toLowerCase()?.includes(search?.toLowerCase()) ||
      val?.studentGRNO?.toLowerCase()?.includes(search?.toLowerCase())
    ) {
      return val;
    }
    return null;
  });
};
